import { createMuiTheme } from '@material-ui/core/styles';
import { green } from '@material-ui/core/colors';

const primaryColor = '#2c2f38',
  secondaryColor = '#373B45',
  activeBgColor = '#434753',
  activeTextColor = '#A1AFDB',
  searchBgColor = '#41454E',
  inputsTextColor = '#B3B5B8',
  primaryTextColor = '#E4E4E5',
  tableRowHover = '#363C50',
  secondaryTextColor = '#7C7E84';

export default createMuiTheme({

  palette: {
    common: {
      primary: `${primaryColor}`,
      secondary: `${secondaryColor}`,
    },
    primary: {
      main: `${primaryColor}`,
    },
    secondary: {
      main: `${secondaryColor}`,
    },
    text: {
      primary: `${primaryTextColor}`,
      secondary: `${secondaryTextColor}`,
    },
  },
  overrides: {
    MuiButton: {
      outlined: {
        border: '2px solid white',
      },
    },
    MuiTableRow:{

    }
  },
});
