import { combineReducers } from 'redux';

import userReducer from './user/user.reducer';
import appReducer from './app/app.reducer';

import configShopReducer from './config-shop/config.reducer';
import configPurchasedReducer from './config-purchased/config.reducer';
import dealsReducer from './deals/deals.reducer';
import releasesReducer from './releases/releases.reducer';
import licensesReducer from './licenses/licenses.reducer';
import corporateLicenseUsersReducer from './licenses/corporate/users/users.reducer';
import corporateLicenseCostsReducer from './licenses/corporate/costs/costs.reducer';
import corporateLicenseActivityReducer from './licenses/corporate/activity/activity.reducer';

import ticketsReducer from './support/tickets/tickets.reducer';

export default combineReducers({
  // Main Reducers
  appOptions: appReducer,
  user: userReducer,

  configShop: configShopReducer,
  configPurchased: configPurchasedReducer,

  // Page Redusers
  licenses: licensesReducer,
  corporateLicenseUsers: corporateLicenseUsersReducer,

  // Chat
  tickets: ticketsReducer,

  //deals
  deals: dealsReducer,
  releases: releasesReducer,


    // activity: corporateLicenseActivityReducer,
    //
    // costs: corporateLicenseCostsReducer
  // personalLicense: {
  //   activity: corporateLicenseActivityReducer,
  //   costs: corporateLicenseCostsReducer
  // }
})