import { appTypes} from './app.types'

export const setCurrentLanguage = lang => ({
  type: appTypes.SET_CURRENT_LANGUAGE,
  payload: lang
});

export const setLanguageModal = bool => ({
  type: appTypes.SET_LANGUAGE_MODAL,
  payload: bool
});

export const setChatState = bool => ({
  type: appTypes.SET_CHAT_STATE,
  payload: bool
});

export const setChatId = bool => ({
  type: appTypes.SET_CHAT_ID,
  payload: bool
})

export const setBuyPLicenseModal = bool => ({
  type: appTypes.SET_BUY_P_LICENSE_MODAL,
  payload: bool
})
export const setBuyCLicenseModal = bool => ({
  type: appTypes.SET_BUY_C_LICENSE_MODAL,
  payload: bool
})
export const setRefillBalanceModal = bool => ({
  type: appTypes.SET_REFILL_BALANCE_MODAL,
  payload: bool
})
export const setRefillLicenseModal = bool => ({
  type: appTypes.SET_REFILL_LICENSE_MODAL,
  payload: bool
})
