import React from 'react';
import MaterialTable from 'material-table';
import './CorporateUsers__styles.scss';
import TableUserProfile from "../../TableComponents/UserProfile/UserProfile";
import ConfigLimits from "../../TableComponents/ConfigLimits/ConfigLimits";
import DaysLeft from "../../TableComponents/DaysLeft/DaysLeft";
import {connect} from "react-redux";
import Switch from '@material-ui/core/Switch';
import { setCosts, setUsers, setActivity, sortTableByType } from "../../../redux/licenses/corporate/users/users.actions";
import { setCurrentLicense, setCurrentTab, setAdditionalScreenCorporate } from "../../../redux/licenses/licenses.actions";
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';


//Icons
import icon__Add from './icons/add.icon.svg';



const CorporateUsers = (props) => {

    const handleChange = (rowData, type, event) => {

        switch (type) {
            case 'activity':
                props.setActivity( !rowData.userOptions.seesActivity,  rowData.user.id );
                break;
            case 'users':
                props.setUsers( !rowData.userOptions.seesUsers,  rowData.user.id );
                break;
            case 'costs':
                props.setCosts( !rowData.userOptions.seesCosts,  rowData.user.id );
                break;
            default:
                break;

        }
    };
    const [tableSort, setSort] = React.useState('date of registration');

    const handleSort = (event) => {
        setSort(event.target.value);

        if(event.target.value === 'date of registration'){
            props.sortTableByType('byRegistration');

        }else if(event.target.value === 'date of last visit' ) {
            props.sortTableByType('byVisit');
        }
    };


    const [state, setState] = React.useState({
        columns: [

            { title: 'Nickname and Login', field: 'getNameAndId()', sorting: false,
                render: rowData => ( <TableUserProfile dataImage={rowData.user.image} dataName={rowData.user.name} dataId={rowData.user.id} /> ) },

            { title: 'Telegram', field: 'user.telegram', sorting: false },
            { title: 'Comment', field: 'user.comment' ,  sorting: false },
            { title: 'Sees activity', field: 'userOptions.seesActivity',  sorting: false,
                render: rowData => (
                    <Switch checked={ rowData.userOptions.seesActivity }
                            onChange={ (e) => {handleChange( rowData, 'activity', e)} }
                            name="userOptions.seesActivity" />) },

            { title: 'Sees users', field: 'userOptions.seesUsers',  sorting: false,
                render: rowData => (
                    <Switch checked={ rowData.userOptions.seesUsers }
                            onChange={ (e) => {handleChange( rowData, 'users', e)} }
                            name="userOptions.seesUsers" />) },

            { title: 'Sees costs', field: 'userOptions.seesCosts',  sorting: false,
                render: rowData => (
                    <Switch checked={ rowData.userOptions.seesCosts }
                        onChange={ (e) => {handleChange( rowData, 'costs', e)} }
                        name="userOptions.seesCosts" />)
            },
            { title: 'Days of using browser left', field: 'userOptions.daysLeft',  sorting: false,
                render: rowData => ( <DaysLeft dataValue={ rowData.userOptions.daysLeft } />  )},
            { title: 'Configs limits in Config Shop', field: 'userOptions.configsLimits',  sorting: false,
                render: rowData => ( <ConfigLimits dataValue={ rowData.userOptions.configsLimits }/>)},
           ]
    });


    return (
        <div id="corporate__users__table" className="сustomStyle">
            <div className="corporate__users__panel">

                <FormControl variant="filled" >
                    <InputLabel id="demo-simple-select-filled-label">Sort users by</InputLabel>
                    <Select
                        labelId="demo-simple-select-filled-label"
                        value={tableSort}
                        onChange={(e)=>{handleSort(e)}}

                    >

                        <MenuItem value={'date of registration'}>Date of registration</MenuItem>
                        <MenuItem value={'date of last visit'}>Date of last visit</MenuItem>
                    </Select>
                </FormControl>


                <div className="icon__add" onClick={()=>{
                    props.setCurrentTab('Generate');
                    props.setAdditionalScreenCorporate(true);
                }}>
                    <img src={icon__Add} alt="add" />
                </div>
            </div>
        <MaterialTable
            title="Editable Example"
            columns={state.columns}
            data={props.usersTabData}
            options={{
                draggable: false,
                showTitle: false,
                paging: false,
                tableLayout: 'fixed'
            }}
        />
        </div>
    );
}

const mapStateToProps = (state) => ({
    usersTabData: state.corporateLicenseUsers.usersTab,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setCosts: (bool, id) => dispatch(setCosts(bool, id)),
        setUsers: (bool, id) => dispatch(setUsers(bool, id)),
        setActivity: (bool, id) => dispatch(setActivity(bool, id)),
        setCurrentTab : (prop) => dispatch(setCurrentTab(prop)),
        setAdditionalScreenCorporate:  (bool) => dispatch(setAdditionalScreenCorporate(bool)),
        sortTableByType : (type) => dispatch(sortTableByType(type)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(CorporateUsers);

