import React, {Fragment} from 'react';
import TabsManager from './../../TabsManager/TabsManager';
import MaterialTable from 'material-table';
import {IconPack} from "../../../Images/IconPack";

const Operations = () => {
    let tabs = [
        {name: 'Operations', link: '/operations'},
        {name: 'Transactions', link: '/transactions'},
    ];

    const valueComponent = (value) => {
        let v = value;

        if(v > 0){
          return (<span className="green">+{value}</span>);
        }else if(v < 0){
            return (<span className="red">{value}</span>);
        }

    }
    const idComponent = (value) => {
        return (<span>{value.operationId}{IconPack.operations.id}</span>);

    }

    const [state, setState] = React.useState({

        columns: [
            { title: 'USD', field: 'value', sorting: false, render: rowdata => valueComponent(rowdata.value) },
            { title: 'Purpose of operation', field: 'operation', sorting: false },
            { title: 'Description', field: 'description',  sorting: false },
            { title: 'Balance, $', field: 'balance',  sorting: false },
            { title: 'Operation ID', field: 'operationId',  sorting: false,  render: rowdata => idComponent(rowdata) },
            { title: 'Date', field: 'date', customSort: (a, b) =>  a.dateIndex - b.dateIndex},
        ],
        data: [
            {
                value: 3000,
                operation: 'Buy config',
                description: 'Ronaldo License: 135195789548',
                balance: 2355.44,
                operationId: 154895327595,
                date: '20 Apr, 19:20:55',
                dateIndex: 20200420192055
            },
            {
                value: -1000,
                operation: 'Buy config',
                description: 'Ronaldo License: 135195789548',
                balance: 5555.44,
                operationId: 154895327595,
                date: '20 Apr, 19:20:55',
                dateIndex: 20200420192055
            },
            {
                value: 1000,
                operation: 'Buy config',
                description: 'Ronaldo License: 135195789548',
                balance: 115.44,
                operationId: 154895327595,
                date: '15 Apr, 19:20:55',
                dateIndex: 20200415192055
            },
            {
                value: 3000,
                operation: 'Buy config',
                description: 'Ronaldo License: 135195789548',
                balance: 2215.44,
                operationId: 154895327595,
                date: '10 Apr, 19:20:35',
                dateIndex: 20200410192035
            },

        ],
    });


    return (
        <Fragment>
            <TabsManager data={tabs}/>
            <div className="content">
            <div className="сustomStyle">
                <MaterialTable
                    title="Editable Example"
                    columns={state.columns}
                    data={state.data}
                    options={{
                        draggable: false,
                        showTitle: false,
                        paging: false,
                        tableLayout: 'fixed'
                    }}
                />
            </div>
            </div>
        </Fragment>
    );
};

export default Operations;

