import React, {Fragment} from 'react';
import MaterialTable from 'material-table';
import {connect} from "react-redux";
import {setChatState, setChatId} from "../../../../redux/app/app.actions";
import MessageComponent from "../ChatComponents/Message";

const MessageTable = (props) => {

    const rowHandler = (data) => {
        props.setChatId(data.chatId);
        props.setChatState(true);
    }

    const messageComponent = (rowData) => {
        return (<Fragment><div className="author">{rowData.data[rowData.data.length-1].author}:</div>{rowData.data[rowData.data.length-1].message}</Fragment>);
    };
    const lastMessageDate = (rowData) => {
        return (<Fragment>{rowData.data[rowData.data.length-1].date}</Fragment>);
    };

    const [state, setState] = React.useState({
        columns: [
            { title: 'Title', field: 'title', sorting: false },
            { title: 'Last message', field: '', sorting: false, render: rowData => (messageComponent(rowData)) },
            { title: 'Creation date', field: 'creationDate',  sorting: false },
            { title: 'Last message', field: '', sorting: false, render: rowData => (lastMessageDate(rowData))},
        ]
    });



    return (
        <div id="message__table" className="сustomStyle">
            <MaterialTable
                columns={state.columns}
                data={props.tickets}
                onRowClick={(event, rowData) => { rowHandler(rowData) }}
                options={{
                    draggable: false,
                    showTitle: false,
                    search:false,
                    paging: false,
                    tableLayout: 'fixed'
                }}
            />
        </div>
    );
}




const mapStateToProps = (state) => ({
    tickets: state.tickets.tickets,
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setChatState: (bool) => dispatch(setChatState(bool)),
        setChatId: (numb) => dispatch(setChatId(numb)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(MessageTable);