const sortDeals = (arr) => {
    arr.sort((a, b) => a.dateCreated > b.dateCreated ? -1 : 1);
}

let currentTicketDataCopy, tickets, currentTicket;
const dealsReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {

            case dealsTypes.CREATE_DEAL:

                let dealsCopy = [...state.tickets, action.payload];

                sortDeals(dealsCopy)

                return {
                    ...state,
                    deals: dealsCopy
                }


        default:
            return state;
    }
};

export default dealsReducer;
