import {configTypes} from './config.types';
import {shopData} from './shopData';
import { IconPack } from './../../Images/IconPack';


const INITIAL_STATE = {
    configShop: {
        sort: {
            device: [
                { title: 'Desktop', icon: IconPack.configIcons.device.desktop },
                { title: 'Tablet', icon: IconPack.configIcons.device.tablet },
                { title: 'Mobile', icon: IconPack.configIcons.device.mobile }
            ],
            os: [
                {
                    title: 'Windows',
                    version: ['10', '8', '7', 'vista'],
                    icon:  IconPack.configIcons.os.win
                },
                {
                    title: 'Linux',
                    version: ['Ubuntu', '2', '3', '4'],
                    icon: IconPack.configIcons.os.linux
                },
                {
                    title: 'MacOs',
                    version: ['1', '2', '3', '4'],
                    icon: IconPack.configIcons.os.mac
                }
            ],
            browser: [
                {
                    title: 'Chrome',
                    version: ['78', '77', '76', '75'],
                    icon: IconPack.configIcons.browser.chrome
                },
                {
                    title: 'Firefox',
                    version: ['10', '20', '30', '40'],
                    icon: IconPack.configIcons.browser.ff,
                },
                {
                    title: 'Safari',
                    version: ['11', '12', '13', '14'],
                    icon: IconPack.configIcons.browser.safari
                },
            ]
        },
        currentSort: {
            device: 'Tablet',
            os: 'Linux',
            osVersion: '10',
            browser: 'Chrome',
            browserVersion: '77',
            isSearchActive: true,
        },
        data: shopData,
        tableData: [],
        selectedData: [],
        searchData: [],
    }
};

let filteredData, tableDataClone;
const configReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case configTypes.UNSELECT_ITEM:
            return {
                ...state,

            };

        case configTypes.SELECT_ITEM:


            let dataClone = [...state.configShop.tableData];
            let selectedItem = null;

            dataClone = dataClone.map(item => {
                if(item.id === action.payload.id) {
                    item.isSelected = !item.isSelected;
                    selectedItem = item;
                }
                return item;
            });


            let selectedDataClone = [...state.configShop.selectedData];
            if(action.payload.isSelected) {
                // push to selectedData[]
                selectedDataClone.push(action.payload);
            }else {
                // del from selectedData[]
                selectedDataClone = selectedDataClone.map((item, key) => {

                    if(typeof item === 'object') {
                        if (item.id !== action.payload.id) {
                            return item
                        } else {
                            selectedDataClone.splice(key, 1);
                        }
                    }

                })
            }



            return {
                ...state,
                configShop: {
                    ...state.configShop,
                    tableData: dataClone,
                    // data: dataClone
                     selectedData: selectedDataClone
                }
            };

        case configTypes.SET_DEVICE_FILTER:
            return {
                ...state,
                configShop: {
                    ...state.configShop,
                    currentSort: {
                        ...state.configShop.currentSort,
                        device: action.payload
                    }

                }
            };

        case configTypes.SET_OS_FILTER:
            return {
                ...state,
                configShop: {
                    ...state.configShop,
                    currentSort: {
                        ...state.configShop.currentSort,
                        os: action.payload
                    }

                }
            };
        case configTypes.SET_OS_VERSION_FILTER:
            return {
                ...state,
                configShop: {
                    ...state.configShop,
                    currentSort: {
                        ...state.configShop.currentSort,
                        osVersion: action.payload
                    }

                }
            };

        case configTypes.SET_BROWSER_FILTER:
            return {
                ...state,
                configShop: {
                    ...state.configShop,
                    currentSort: {
                        ...state.configShop.currentSort,
                        browser: action.payload
                    }

                }
            };
        case configTypes.SET_BROWSER_VERSION_FILTER:
            return {
                ...state,
                configShop: {
                    ...state.configShop,
                    currentSort: {
                        ...state.configShop.currentSort,
                        browserVersion: action.payload
                    }

                }
            };

        case configTypes.UPDATE_DATA:




            filteredData = [...state.configShop.data].filter((tableRow) => {
                if (tableRow.deviceObject.title === state.configShop.currentSort.device) {
                    return tableRow;
                }
            });

            filteredData = filteredData.filter((tableRow) => {
                if (tableRow.osObject.title === state.configShop.currentSort.os) {
                    return tableRow;
                }
            });


            filteredData = filteredData.filter((tableRow) => {
                if (tableRow.osObject.version === state.configShop.currentSort.osVersion) {
                    return tableRow;
                }
            });
            filteredData = filteredData.filter((tableRow) => {
                if(tableRow.browserObject.title === state.configShop.currentSort.browser) {
                    return tableRow;
                }
            });
            filteredData = filteredData.filter((tableRow) => {
                if(tableRow.browserObject.version === state.configShop.currentSort.browserVersion) {
                    return tableRow;
                }
            });


            return {
                ...state,
                configShop: {
                    ...state.configShop,
                    searchData: filteredData,
                    tableData: filteredData
                }
            };



        case configTypes.SET_BROWSER_VERSION_FILTER:
            return {
                ...state,
                configShop: {
                    ...state.configShop,
                    currentSort: {
                        ...state.configShop.currentSort,
                        browserVersion: action.payload
                    }

                }
            };

        case configTypes.SEARCH_DATA:
            let vals;
            let searchedValues = [];

            filteredData = [...state.configShop.tableData].map(x => { // get array

                let isSearchTrueForThisElem = false;
                vals = Object.values(x).some((item , i) => { // get data obj

                    if(typeof item !== 'object') {


                        if(item.toString().toLowerCase().includes(action.payload.toLowerCase()) &&  !isSearchTrueForThisElem ){
                            isSearchTrueForThisElem = true;
                            searchedValues.push(x)

                         }
                    }

                    });
            });

            return {
                ...state,
                configShop: {
                    ...state.configShop,
                    searchData: searchedValues
                }
            };


        default:
            return state
    }
};

export default configReducer;
