import React, {Fragment} from 'react';
import TabsManager from './../../TabsManager/TabsManager';
import MaterialTable from "material-table";

import { IconPack } from './../../../Images/IconPack'


const Transactions = () => {
    let tabs = [
        {name: 'Operations', link: '/operations'},
        {name: 'Transactions', link: '/transactions'},
    ];

    const valueComponent = (value) => {
        let v = value.value;
        if(value.status){
            return (<span className="green">{v}</span>);
        }else {
            return (<span className="red">{v}</span>);
        }
    }
    const statusComponent = (value) => {
        let v = value.statusTitle;
        if(value.status){
            return (<span className="green">{IconPack.operations.statusGood}{v}</span>);
        }else {
            return (<span className="red">{IconPack.operations.statusBad}{v}</span>);
        }
    }
    const idComponent = (value) => {
        return (<span>{value.transactionId}{IconPack.operations.id}</span>);

    }

    const [state, setState] = React.useState({

        columns: [
            { title: 'USD', field: 'value', sorting: false, render: rowdata => valueComponent(rowdata) },
            { title: 'Status', field: 'statusTitle', sorting: false, render: rowdata => statusComponent(rowdata) },
            { title: 'Payment system', field: 'ps',  sorting: false },
            { title: 'Description', field: 'description',  sorting: false },
            { title: 'Transaction ID', field: 'transactionId',  sorting: false, render: rowdata => idComponent(rowdata) },
            { title: 'Date', field: 'date', customSort: (a, b) =>  a.dateIndex - b.dateIndex},
        ],
        data: [
            {
                value: 3000,
                operation: 'Buy config',
                status: true,
                ps: 'Bitcoin',
                statusTitle: 'Completed',
                description: 'Top up balance',
                transactionId: 154895327595,
                date: '20 Apr, 19:20:55',
                dateIndex: 20200420192055
            },
            {
                value: 1000,
                operation: 'Buy config',
                status: false,
                ps: 'Bitcoin',
                statusTitle: 'Session Expired',
                description: 'Top up balance',
                transactionId: 154895327595,
                date: '20 Apr, 19:20:55',
                dateIndex: 20200420192055
            },

        ],
    });


    return (
        <Fragment>
            <TabsManager data={tabs}/>
            <div className="content">
                <div className="сustomStyle">
                    <MaterialTable
                        title="Editable Example"
                        columns={state.columns}
                        data={state.data}
                        options={{
                            draggable: false,
                            showTitle: false,
                            paging: false,
                            tableLayout: 'fixed'
                        }}
                    />
                </div>
            </div>
        </Fragment>
    );
};

export default Transactions;