import {ticketsTypes} from './tickets.types';
import { data1 } from './chatMessagesObject/messages.data.id1';
import { data2 } from './chatMessagesObject/messages.data.id2';
import { data3 } from './chatMessagesObject/messages.data.id3';


const INITIAL_STATE = {
    tickets: [
        {
            title: 'Plan to take over the universe',
            subject: 'Questions about buying a license',
            creationDate: '3 feb, 18:30:30',
            data: data1,
            chatId: 1,
        },
        {
            title: 'Test Title',
            subject: 'Test Subject',
            creationDate: '3 feb, 18:30:30',
            data: data2,
            chatId: 2,
        },
        {
            title: 'Test Title',
             subject: 'Test Subject',
            creationDate: '3 feb, 18:30:30',
            data: data3,
            currentMessage: '',
            chatId: 3,
        },
        {
            title: 'Test Title',
            subject: 'Test Subject',
            creationDate: '3 feb, 18:30:30',
            data: data3,
            chatId: 4,
        }
    ]
};

const sortMessages = (arr) => {
    arr.sort((a, b) => a.data[a.data.length-1].dateIndex > b.data[b.data.length-1].dateIndex ? -1 : 1);
}

let currentTicketDataCopy, tickets, currentTicket;
const ticketsReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ticketsTypes.ADD_MESSAGE:

            tickets = [...state.tickets];

            currentTicket = tickets.find(curTicket => {
                return curTicket.chatId === action.payload.chatId;
            });


            if(currentTicket) {
                currentTicket.data =  [...currentTicket.data, action.payload.message];

                let ticketsCopy = tickets.filter(ticket => {
                    return ticket.chatId !== currentTicket.chatId
                })

                ticketsCopy = [...ticketsCopy, currentTicket];

                sortMessages(ticketsCopy)

                return {
                    ...state,
                    tickets: ticketsCopy
                }
            }

            case ticketsTypes.CREATE_TICKET:

                let ticketsCopy = [...state.tickets, action.payload];

                sortMessages(ticketsCopy)

                return {
                    ...state,
                    tickets: ticketsCopy
                }

        default:
            return state;
    }
};

export default ticketsReducer;
