import React from 'react';
import './style.scss'
import {useHistory} from "react-router-dom";

import AdditionalTabForLicense from './PersonalLicense/PersonalLicense';
import {setCurrentLicense, setCurrentTab, setAdditionalScreenCorporate} from "../../redux/licenses/licenses.actions";
import {setRefillLicenseModal} from "../../redux/app/app.actions";
import {connect} from "react-redux"; // setRefillLicenseModal


const TabsManager = (props) => {

    let history = useHistory();
    let path = history.location.pathname;
    let tabs, tabsComponent;
    let additionalComponent = '';
    let tabItemClassName = 'tabItem';
    let balanceString = props.licenses.corporateLicense.balance;
    let balanceArray = balanceString.split('.');

    if (props.data) {
        tabs = props.data.map((tab, i) => {
            let tabItem;
            if (path === tab.link) {
                tabItemClassName = 'tabItem tabItem--active';
            } else {
                tabItemClassName = 'tabItem';
            }

            tabItem = (<div className={tabItemClassName} key={i.toString()} onClick={() => {
                props.setCurrentTab(tab.name);
                history.push(tab.link);
            }}>
                {tab.name}
            </div>);

            return tabItem;
        });
    }

    if (props.licenseComponent) {
        additionalComponent = (<div className="tabLicense">
            <AdditionalTabForLicense data={props.licenses} user={props.currentUser}/>
        </div>);
    }

    let licenseBalance;
    if(props.licenseBalance) {

        licenseBalance = (<div className="licenseBalance userBalance ">
                <svg width="12" height="21" viewBox="0 0 12 21" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M8.61719 9.99219L4.08594 8.66406C3.46094 8.50781 2.99219 8 2.875 7.375C2.71875 6.35938 3.5 5.5 4.47656 5.5H7.32812C7.95312 5.5 8.57812 5.69531 9.08594 6.08594C9.32031 6.24219 9.67188 6.20312 9.86719 6.00781L10.7656 5.10938C11.0391 4.83594 11 4.40625 10.7266 4.17188C9.75 3.42969 8.57812 3 7.32812 3H7.25V1.125C7.25 0.8125 6.9375 0.5 6.625 0.5H5.375C5.02344 0.5 4.75 0.8125 4.75 1.125V3H4.63281C2.71875 3 0.921875 4.25 0.492188 6.125C-0.0546875 8.3125 1.27344 10.4609 3.34375 11.0469L7.875 12.375C8.5 12.5312 9.00781 13.0391 9.08594 13.6641C9.24219 14.6406 8.46094 15.5 7.52344 15.5H4.63281C4.00781 15.5 3.38281 15.3047 2.875 14.9531C2.64062 14.7969 2.28906 14.8359 2.09375 15.0312L1.19531 15.9297C0.921875 16.2031 0.960938 16.6328 1.23438 16.8672C2.21094 17.6094 3.38281 18 4.63281 18H4.75V19.875C4.75 20.2266 5.02344 20.5 5.375 20.5H6.625C6.9375 20.5 7.25 20.2266 7.25 19.875V18H7.32812C9.24219 18 11.0391 16.7891 11.4688 14.9141C12.0156 12.7266 10.6875 10.5781 8.61719 9.99219Z" fill="white" fillOpacity="0.6"/>
                </svg>
                <span onClick={()=>{history.push('/licenses/1234567890/costs')}}>{balanceArray[0]}</span>
                <span className="afterpoint" onClick={()=>{history.push('/licenses/1234567890/costs')}}>.{balanceArray[1]}</span>

                <div className="add-button" onClick={()=>{props.setRefillLicenseModal(true)}}></div>
        </div>);
    }


    if (props.licenses.currentLicense === 'Corporate' && props.licenses.corporateLicense.additionalScreen === true) {

        let generateClass = 'tabItem';
        let inviteClass = 'tabItem';
        if (props.licenses.currentTab === 'Generate') {
            generateClass = 'tabItem tabItem--active';
        } else if (props.licenses.currentTab === 'Invite') {
            inviteClass = 'tabItem tabItem--active';
        }

        tabsComponent = (<div className="tabWrapper">
            <div className='tabItem__arrow' onClick={() => {
                props.setCurrentTab('Users');
                props.setAdditionalScreenCorporate(false);
            }}>
                <svg width="10" height="14" viewBox="0 0 10 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M1.0625 6.5C0.78125 6.78125 0.78125 7.25 1.0625 7.53125L7.125 13.625C7.4375 13.9062 7.90625 13.9062 8.1875 13.625L8.90625 12.9062C9.1875 12.625 9.1875 12.1562 8.90625 11.8438L4.09375 7L8.90625 2.1875C9.1875 1.875 9.1875 1.40625 8.90625 1.125L8.1875 0.40625C7.90625 0.125 7.4375 0.125 7.125 0.40625L1.0625 6.5Z" fill="white" fillOpacity="0.6"/>
                </svg>
            </div>
            <div className={tabItemClassName} onClick={() => {
                    props.setCurrentTab('Generate');
                }}>
                Generate
            </div>
            <div className={tabItemClassName} onClick={() => {
                    props.setCurrentTab('Invite');
                }}>
                Invite
            </div>
        </div>);
    } else {
        tabsComponent = (<div className="tabWrapper">{tabs} {props.licenseBalance ? licenseBalance : null}</div>);
    }


    return (
        <div id="TabsManager">

            {additionalComponent}

            {tabsComponent}

        </div>
    );
};

const mapStateToProps = (state) => ({
    licenses: state.licenses.licenses,
    currentUser: state.user.currentUser,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setCurrentLicense: (string) => dispatch(setCurrentLicense(string)),
        setCurrentTab: (string) => dispatch(setCurrentTab(string)),
        setAdditionalScreenCorporate: (bool) => dispatch(setAdditionalScreenCorporate(bool)),
        setRefillLicenseModal: (bool) => dispatch(setRefillLicenseModal(bool)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(TabsManager);