import React, {Fragment} from 'react';
import MaterialTable from 'material-table';
import {connect} from "react-redux";
import {setChatState, setChatId} from "../../../../redux/app/app.actions";
import {getDate} from "../../../../redux/Utils";

const DealsTable = (props) => {

    const lastMessageDate = (rowData) => {
        return (<Fragment>{rowData.dateCreated}</Fragment>);
    };

    const tableAction = (rowData) => {
        return (<div className="tableAction" onClick={()=>{tableActionHendler(rowData)}}><svg width="5" height="14" viewBox="0 0 5 14" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.5 5.29688C1.48438 5.29688 0.671875 6.13477 0.671875 7.125C0.671875 8.14062 1.48438 8.95312 2.5 8.95312C3.49023 8.95312 4.32812 8.14062 4.32812 7.125C4.32812 6.13477 3.49023 5.29688 2.5 5.29688ZM0.671875 2.65625C0.671875 3.67188 1.48438 4.48438 2.5 4.48438C3.49023 4.48438 4.32812 3.67188 4.32812 2.65625C4.32812 1.66602 3.49023 0.828125 2.5 0.828125C1.48438 0.828125 0.671875 1.66602 0.671875 2.65625ZM0.671875 11.5938C0.671875 12.6094 1.48438 13.4219 2.5 13.4219C3.49023 13.4219 4.32812 12.6094 4.32812 11.5938C4.32812 10.6035 3.49023 9.76562 2.5 9.76562C1.48438 9.76562 0.671875 10.6035 0.671875 11.5938Z" fill="white" fillOpacity="0.6"/>
        </svg></div>);
    };

    const tableActionHendler = (data) => {
        // open popup
        // insert data


    }

    const [state, setState] = React.useState({
        columns: [
            { title: 'Deal title', field: 'title', sorting: false },
            { title: 'Status', field: 'status', sorting: false },
            { title: 'Seller', field: 'seller', sorting: false },
            { title: 'Buyer', field: 'buyer', sorting: false },
            { title: 'Amount', field: 'amount', sorting: false,  },
            { title: 'Deal ID', field: 'dealId',  sorting: false },
            { title: 'Date Created', field: 'dateCreated', sorting: false, render: rowData => (lastMessageDate(rowData))},
            { title: '', field: '', sorting: false, render: rowData => (tableAction(rowData))},
        ]
    });


    return (
        <div id="deals__table" className="сustomStyle">
            <MaterialTable
                columns={state.columns}
                data={props.deals}

                options={{
                    draggable: false,
                    search:false,
                    showTitle: false,
                    paging: false,
                    tableLayout: 'fixed'
                }}
            />
        </div>
    );
}




const mapStateToProps = (state) => ({
    deals: state.deals.deals,
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setChatState: (bool) => dispatch(setChatState(bool)),
        setChatId: (numb) => dispatch(setChatId(numb)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(DealsTable);